import cx from "classnames"
import React from "react"
import { useSelector } from "react-redux"
import { Link, useHistory } from "react-router-dom"
import { selectCategories } from "../store/catalog"
import "./CategoryDisplay.css"
import StepTitle from "./StepTitle"

export default function CategoryDisplay() {
  const cats = useSelector(selectCategories)
  const history = useHistory()
  const categoryMarkup = cats?.map((cat) => (
    <Link
      to={"/accessory-categories/" + cat.id}
      className="category-link"
      key={cat.id}
    >
      <div className={cx("category-icon")}>
        <img src={process.env.REACT_APP_ASSETS_URL + cat.icon?.url} alt="" />
        <span>{cat.name}</span>
      </div>
    </Link>
  ))

  return (
    <React.Fragment>
      <StepTitle
        title={"Accessory Categories"}
        showBackButton={true}
        onBackClick={history.goBack}
      />
      <div className="category-listing">{categoryMarkup}</div>
    </React.Fragment>
  )
}
