import React from "react"
import { useSelector } from "react-redux"
import { useHistory, useParams } from "react-router-dom"
import { selectAccessoryById } from "../store/catalog"
import "./App.css"

export default function AccessoryDetails() {
  // const dispatch = useDispatch();
  const { id } = useParams<{id: string}>()
  const accessory = useSelector(selectAccessoryById(id))
  let history = useHistory()
  console.log("acc ", accessory)
  return (
    <div>
      <h3>{accessory?.name}</h3>
      <h5>${accessory?.rrp}</h5>
      <p>{accessory?.description}</p>
      <button
        className="primary-button back-button"
        // to={"/accessory-categories/"}
        onClick={history.goBack}
      >
        Back
      </button>
    </div>
  )
}
