import cx from "classnames"
import React, { useEffect, useState } from "react"
import { useDispatch } from "react-redux"
import { Link, Redirect } from "react-router-dom"
import { nextBuilderStep, prevBuilderStep } from "../store/ui"
import StepTitle from "./StepTitle"

export const Wizard = (props) => {
  const steps = React.Children.toArray(props.children)
  
  return (
    <React.Fragment>
      <Template path="/:currentPath" steps={steps} currentStep={props.currentStep}/>
      {/* <Redirect exact from="/" to={steps[0].props.path} noThrow /> */}
    </React.Fragment>
  )
}

export const Step = (props) => {
  const { onValid, children } = props
  //children can be elements, or functions which return a child element
  useEffect(() => {
    if (React.isValidElement(children)) {
      onValid(true)
    }
  })
  if (!React.isValidElement(children)) {
    return children(onValid)
  } else {
    return children
  }
}

function Template({ currentPath, steps, currentStep }) {
  const dispatch = useDispatch()
  const [valid, setValid] = useState(false)
  const [lastIndex, setLastIndex] = useState(-2)


  // const currentIndex = steps.findIndex((step) => "/" + step.props.path === path)
  const currentIndex = currentStep

  if (lastIndex !== currentIndex) {
    setValid(false)
    setLastIndex(currentIndex)
  }

  const prevStep = currentIndex > 0 && steps[currentIndex - 1].props
  const nextStep = currentIndex < steps.length - 1 && steps[currentIndex + 1].props

  const handleValid = (isValid) => {
    setValid(isValid)
  }
  if (currentIndex < 0) {
    return <NoMatch path={steps[0].props.path} />
  }

  const StepTitles = steps.map((step, idx) => <StepTitle title={idx + 1 + ". " + step.props.title} isActive={currentIndex===idx} key={idx} />);

  return (
    <div>
      <div className="step-title-container">
        {StepTitles}
      </div>
      
      {/* <StepTitle
        title={currentIndex + 1 + ". " + steps[currentIndex].props.title}
        showBackButton={currentIndex > 0}
        onBackClick={() => history.push(prevStep.path)}
      /> */}

      {/* clone the step element, adding the onvalid prop*/}
      {React.cloneElement(steps[currentIndex], { onValid: handleValid })}

      <footer className="wizard-footer">
        <Button className={cx("back-button", "secondary-button", {disabled: !prevStep})}
          onClick={() => dispatch(prevBuilderStep())}
          title={prevStep.description}
        >Back</Button>
        <Button
          className={cx("primary-button", { disabled: !valid })}
          onClick={() => dispatch(nextBuilderStep())}
          title={nextStep.description}
        >Next</Button>
      </footer>
    </div>
  )
}

function NoMatch(props) {
  return <Redirect to={props.path} noThrow />
}

function Button({ path, ...props }) {
  return (
    <Link
      to={`../${path}`}
      className={path ? "btn btn-primary" : "invisible"}
      {...props}
    />
  )
}
