import React from "react"
import { useSelector } from "react-redux"
import { BrowserRouter, Route, Switch } from "react-router-dom"
import { selectFullBuild } from "../store/build"
import { selectCustomerDetails } from "../store/customer"
import { selectSettings } from "../store/settings"
import { selectCurrentStep } from "../store/ui"
import AccessoriesInCategory from "./AccessoriesInCategory"
import AccessoriesPicker from "./AccessoriesPicker"
import AccessoryDetails from "./AccessoryDetails"
import AddressForm from "./AddressForm"
import "./App.css"
import BuildStatusBox from "./BuildStatusBox"
import CategoryDisplay from "./CategoryDisplay"
import ContactDetailsForm from "./ContactDetailsForm"
import DeliveryDetailsForm from "./DeliveryDetailsForm"
import ModelDetails from "./ModelDetails"
import ModelPicker from "./ModelPicker"
import ProductDetails from "./ProductDetails"
import ProductPicker from "./ProductPicker"
import ThankYouScreen from "./ThankYouScreen"
import { Step, Wizard } from "./Wizard"

declare global {
  interface Window {szReactPlugin:any}
}
function App() {
  const settings = useSelector(selectSettings)
  const currentStep = useSelector(selectCurrentStep)

  const allDetails = {
    ...useSelector(selectFullBuild), 
    customer: useSelector(selectCustomerDetails)
  };


  const sendMail = () =>{

    if(window.szReactPlugin){
      fetch(window.szReactPlugin?.email_endpoint, {

        method: "POST",
        // credentials: 'same-origin',
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(allDetails)
      })
        .then((response) => response.json())
        .then((data) => {
          if (data) {
            console.log(data)
          }
        })
        .catch((error) => {
          console.log('[WP Pageviews Plugin]');
          console.error(error);
        });
    }


  }
  return (
    <div className="app">
      <button onClick={() => sendMail()}>Send Mail</button>
      <BrowserRouter>
        <div className="app-container">
          <div className="primary-container">
            <main>
              <h1 className="small-caps">Configure Your Camper</h1>
              <BuildStatusBox />
              <Switch>
                <Route path="/select-model/:id">
                  <ModelDetails />
                </Route>
                <Route path="/select-product/:id">
                  <ProductDetails />
                </Route>
                <Route path="/accessory-categories/:id">
                  <AccessoriesInCategory />
                </Route>
                <Route path="/accessory-categories/">
                  <CategoryDisplay />
                </Route>
                <Route path="/accessories/:id">
                  <AccessoryDetails />
                </Route>
                <Route path="/">
                  <Wizard currentStep={currentStep}>
                    {settings?.require_user_contact_details_upfront &&
                    <Step path="6-address-details" title="Your Details">
                      {(handleValid: ((valid: boolean) => void)) => (
                        <AddressForm onValid={handleValid} />
                      )}
                    </Step>
                    }
      
                    <Step path="1-select-product" title="Select Product">
                      {(handleValid: ((valid: boolean) => void)) => (
                        <ProductPicker onValid={handleValid} />
                      )}
                    </Step>
                    <Step path="2-select-model" title="Select Model">
                      {(handleValid: ((valid: boolean) => void)) => (
                        <ModelPicker onValid={handleValid} />
                      )}
                    </Step>
                    <Step
                      path="3-add-accessories"
                      title="Add Accessories"
                    >
                      <AccessoriesPicker />
                    </Step>
                    {!settings?.require_user_contact_details_upfront &&
                    <Step path="5-contact-details" title="Your Details">
                      {(handleValid: ((valid: boolean) => void)) => (
                        <ContactDetailsForm onValid={handleValid} />
                      )}
                    </Step>
                    }
                    <Step path="4-delivery-details" title="Delivery Details">
                      {(handleValid: (valid: boolean) => void) => (
                        <DeliveryDetailsForm onValid={handleValid} />
                      )}
                    </Step>
                    <Step path="6-address-details" title="Your Address">
                      {(handleValid: (valid: boolean) => void) => (
                        <AddressForm onValid={handleValid} />
                      )}
                    </Step>

                    <Step path="thankyou" title="Confirmation">
                      <ThankYouScreen/>
                    </Step>
                  </Wizard>
                  {/* <BuildSummarySidebar/> */}
                </Route>
              </Switch>
            </main>
          </div>
        </div>
      </BrowserRouter>
    </div>
  )
}
          
export default App
          