import React from "react"
import { useSelector } from "react-redux"
import { useHistory, useParams } from "react-router-dom"
import { selectModelById } from "../store/catalog"
import "./App.css"
import "./ModelDetails.css"

export default function ModelDetails() {
  const { id } = useParams<{id:string}>()

  const model = useSelector(selectModelById(parseInt(id)))
  let history = useHistory()

  return (
    <>
      <div className="model-details-section">
        <div className="model-image-container">
        WIP
          {model && <img src={"" + process.env.REACT_APP_ASSETS_URL + model?.featured_image?.url} alt="" />}
        </div> 
        <div className="model-details">
          <h3>{model && model?.name}</h3>
          <h5>${model && model.rrp}</h5>
          <p>{model && model.description}</p>
        </div>
      </div>
      <button onClick={history.goBack}>Back</button>
    </>
  )
}
