import cx from "classnames"
import React, { useState } from "react"
import { VscChevronLeft } from "react-icons/vsc"
import { useSelector } from "react-redux"
import { selectBuildImage } from "../store/build"
import AccessoryDetailsSidebar from "./AcccessoryDetailsSidebar"
import AccessoriesList from "./AccessoriesList"
import "./AccessoriesPicker.css"

export default function AccessoriesPicker() {

  const image = useSelector(selectBuildImage)
  const [showDetails, setShowDetails] = useState(false)
  const [showingAccessoryId, setshowingAccessoryId] = useState("1")

  const showAccessory = (accessoryId:string) => {
    console.log('showing accessory ' + accessoryId)
    setShowDetails(true)
    setshowingAccessoryId(accessoryId)
  }
  
  return (
    <div className="accessories-section">
      <div className="model-image-container">
        <img src={"" + process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
      </div>
      <div className="sidebar accessories-sidebar">
        <div className={cx("sidebar-menu-1", {active: !showDetails})} >
          <AccessoriesList onDetailsClick={(id:string) => showAccessory(id) } />
        </div>
        {/* {accessoriesMarkup} */}
        <div className={cx("sidebar-menu-2", {active: showDetails})}>
          <div style={{cursor:"pointer"}} onClick={() => setShowDetails(false)}><VscChevronLeft /> Back</div>
          <AccessoryDetailsSidebar id={showingAccessoryId}></AccessoryDetailsSidebar>
        </div>
      </div>

    </div>  )
}
