import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState } from "./store"
interface uiState {
  builderStep: number
}

const initialState: uiState = {
  builderStep: 0,
}

export const uiSlice = createSlice({
  name: "ui",
  initialState,
  reducers: {
    nextBuilderStep: (state, action: PayloadAction) => {
      state.builderStep++
    },
    prevBuilderStep: (state, action: PayloadAction) => {
      if (state.builderStep > 0) {
        state.builderStep--
      }
    },
    setBuilderStep: (state, action: PayloadAction<number>) => {
      state.builderStep = action.payload
    },
  },
})

export const {
  nextBuilderStep,
  prevBuilderStep,
  setBuilderStep,
} = uiSlice.actions
export const selectCurrentStep = (state: RootState) => state.ui.builderStep

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
// export const selectBuilderStep = (state: RootState) => state.ui.builderStep;

export default uiSlice.reducer
