import React from "react"
import { useDispatch, useSelector } from "react-redux"
import { Link, useHistory, useParams } from "react-router-dom"
import {
  addAccessory,
  removeAccessory,
  selectBuildAccessoryIds
} from "../store/build"
import {
  selectAccessoryCatById,
  selectOptionalAccessoriesInCat
} from "../store/catalog"
import Checkbox from "./Checkbox"
import StepTitle from "./StepTitle"

export default function AccessoriesInCategory() {
  const { id } = useParams<{id:string}>()
  const dispatch = useDispatch()
  const accessories = useSelector(selectOptionalAccessoriesInCat(id))
  const buildAccessories = useSelector(selectBuildAccessoryIds)
  const category = useSelector(selectAccessoryCatById(id))
  let history = useHistory()
  const markup = accessories?.map((acc) => (
    <div className="accessory-short_description" key={acc.id}>
      <h3>{acc.name}</h3>
      <p>{acc.short_description}</p>
      <Checkbox
        onChange={(val: React.ChangeEvent<HTMLInputElement>) => {
          console.log(val)
          val.target.checked
            ? dispatch(addAccessory(acc.id))
            : dispatch(removeAccessory(acc.id))
        }}
        checked={buildAccessories.includes(acc.id)}
      />

      <Link to={"/accessories/" + acc.id}>Read More &gt;</Link>
    </div>
  ))

  return (
    <div>
      <StepTitle
        title={"Category: " + category?.name || ""}
        showBackButton={true}
        onBackClick={history.goBack}
      />

      {markup}

      <Link className="primary-button" to="/3-select-accessories">
        Done
      </Link>
    </div>
  )
}
