import cx from "classnames"
import React, { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { NavLink } from "react-router-dom"
import { selectBuildProduct, setProduct } from "../store/build"
import { selectProducts } from "../store/catalog"
import { formatCurrency } from "../utils/utils"
import "./ProductPicker.css"


interface props {
  onValid: (isValid: boolean) => void
}

export default function ProductPicker(props: props) {
  const dispatch = useDispatch()
  const products = useSelector(selectProducts)
  const product = useSelector(selectBuildProduct)

  //if one of these products is selected, allow progress
  useEffect(() => {
    if (products.find((prod) => prod.id === product?.id)) {
      props.onValid(true)
    }
  })

  let buildItemsMarkup
  if (products) {
    buildItemsMarkup = products.map((x) => {
      return (
        <div
          className={cx("product", { selected: product?.id === x.id })}
          key={x.id}
          onClick={() => {
            dispatch(setProduct(x.id))
          }}
        >
          <div className="product-thumb-wrapper">
            {x.featured_image?.url && (
              <img
                className="product-thumb"
                alt={x.name}
                src={x.featured_image?.url}
              />
            )}
          </div>
          <div className="select-product-details">
            <h3 className="product-title">{x.name}</h3>
            <p className="product-short_description">{x.short_description}</p>
            <div className="product-bottom-line">
              {x.rrp && <h4 className="product-price">Starts at {formatCurrency(x.rrp)}</h4>}
              <NavLink to={`/select-product/${x.id}`} className="secondary-link">View Specs</NavLink>
            </div>
          </div>
        </div>
      )
    })
  }

  return (
    <div>
      <div className="product-listing">
        {buildItemsMarkup}
        {/* <Link className="primary-button" to="/select-accessories/">Select Model ></Link> */}
      </div>
    </div>
  )
}
